<template>
  <ul id="video-player" class="list-group detail mb-5">
    <li class="list-group-item frame">
      <div class="ratio ratio-16x9">
        <iframe :src="videoSource" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
      </div>
    </li>
    <li class="list-group-item fs-4 fw-bold title">
      {{ selectedVideo.snippet.title }}
    </li>
    <li class="list-group-item desc">
      {{ selectedVideo.snippet.description }}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'VideoDetail',
  props: {
    selectedVideo: Object,
  },
  computed: {
    videoSource() {
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`
    }
  },
}
</script>

<style>
.frame {
  background-color: rgb(200, 245, 226);
}

.title {
  background-color: rgb(241, 255, 249);
}

.desc {
  background-color: rgb(238, 238, 238);
}

</style>