<template>
  <li class="list-group-item video-item" @click="onClick">
    <img class="me-3" :src="VideoImageUrl" alt="video-thumbnail">
    <div class="media-body">
      {{ video.snippet.title }}
    </div>
    <!-- <iframe width="560" height="315" :src="src" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe> -->
    
  </li>
</template>

<script>
export default {
  name: "VideoListItem",
  props: {
    video: Object,
  },
  data() {
    return {
      src: `https://www.youtube.com/embed/${this.video.id.videoId}`,
    }
  },
  methods: {
    onClick() {
      this.$emit('video-click', this.video)
    },
  },
  computed: {
    VideoImageUrl() {
      return this.video.snippet.thumbnails.default.url
    }
  }
}
</script>

<style scoped>
.video-item {
  display: flex;
  cursor: pointer;
}
.video-item:hover {
  background-color:rgb(230, 255, 247);
}
</style>