<template>
  <div class="container" id="app">
    <h1>Youtube Browser</h1>
    <!-- 3 -->
    <SearchBar @fetch-videos="onFetchVideos"/>
    <VideoDetail v-if="isVideoSelected" :selectedVideo="SelectedVideo" />
    <VideoList :videos="videos" @video-click="onVideoClick"/>
  </div>
</template>

<script>
// 1
import SearchBar from './components/SearchBar.vue'
import VideoList from './components/VideoList.vue'
import VideoDetail from './components/VideoDetail.vue'

export default {
  name: 'App',
  // 2
  components: {
    SearchBar,
    VideoList,
    VideoDetail,
  },
  methods: {
    onFetchVideos(videos) {
      this.videos = videos
    },
    onVideoClick(video) {
      this.SelectedVideo= video
      window.scrollTo({top:'0', behavior:'smooth'});
    },
  },
  data() {
    return {
      videos: [],
      SelectedVideo: {},
    }
  },
  computed: {
    isVideoSelected(){
      return !!Object.keys(this.SelectedVideo).length
    },
  }
}
</script>

