<template>
  <ul class="list-group">
    <h2 class="mb-4">Videos</h2>

    <VideoListItem  
      v-for="video in videos" 
      :key="video.etag" 
      :video="video"
      @video-click="onVideoClick"
    />
  </ul>
</template>

<script>
import VideoListItem from './VideoListItem.vue'

export default {
  name: 'VideoList',
  props: {
    videos: Array,
  },
  components: {
    VideoListItem,
  },
  methods: {
    onVideoClick(video) {
      this.$emit('video-click', video)
    }
  }
}
</script>

